import { usersAPI } from '../api/api';

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_TOTAL_COUNT = 'SET_TOTAL_COUNT';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS';
const SHIFT_PAGES_LEFT = 'SHIFT_PAGES_LEFT';
const SHIFT_PAGES_RIGHT = 'SHIFT_PAGES_RIGHT';
const TOGGLE_IS_FRIENDS = 'TOGGLE_IS_FRIENDS';
const SET_FRIENDS = 'SET_FRIENDS';

const initialState = {
  users: [],
  friends: [],
  pageSize: 4,
  totalUsersCount: 0,
  currentPage: 1,
  beginPage: 0,
  endPage: 12,
  isFetching: false,
  isFriends: false,
  followingInProgress: [],
  sidebarSize: 5,
};

const usersReducer = (state = initialState, action) => {
  switch (action.type) {
    case FOLLOW:
      return {
        ...state,
        users: state.users.map((user) => {
          if (user.id === action.userId) {
            return { ...user, followed: true };
          }
          return user;
        }),
      };
    case UNFOLLOW:
      return {
        ...state,
        users: state.users.map((user) => {
          if (user.id === action.userId) {
            return { ...user, followed: false };
          }
          return user;
        }),
      };

    case SET_USERS:
      return {
        ...state,
        users: action.users,
      };

    case SET_FRIENDS:
      return {
        ...state,
        friends: action.friends,
      };

    case SET_CURRENT_PAGE:
      return {
        ...state,
        currentPage: action.currentPage,
      };
    case SET_TOTAL_COUNT:
      return {
        ...state,
        totalUsersCount: action.totalUsersCount,
      };

    case TOGGLE_IS_FETCHING: {
      return { ...state, isFetching: action.isFetching };
    }

    case TOGGLE_IS_FOLLOWING_PROGRESS: {
      return {
        ...state,
        followingInProgress: action.isFetching
          ? [...state.followingInProgress, action.userId]
          : [state.followingInProgress.filter((id) => id !== action.userId)],
      };
    }

    case SHIFT_PAGES_LEFT: {
      return { ...state, beginPage: action.beginPage, endPage: action.endPage };
    }
    case SHIFT_PAGES_RIGHT: {
      return { ...state, beginPage: action.beginPage, endPage: action.endPage };
    }

    case TOGGLE_IS_FRIENDS: {
      return { ...state, isFriends: action.isFriends };
    }

    default:
      return state;
  }
};

export const unfollowSuccess = (userId) => ({ type: UNFOLLOW, userId });
export const followSuccess = (userId) => ({ type: FOLLOW, userId });
export const setUsers = (users) => ({ type: SET_USERS, users });
export const setFriends = (friends) => ({ type: SET_FRIENDS, friends });
export const setCurrentPage = (currentPage) => ({
  type: SET_CURRENT_PAGE,
  currentPage,
});
export const setTotalUsersCount = (totalUsersCount) => ({
  type: SET_TOTAL_COUNT,
  totalUsersCount,
});
export const toggleIsFetching = (isFetching) => ({
  type: TOGGLE_IS_FETCHING,
  isFetching,
});
export const toggleFollowingProgress = (isFetching, userId) => ({
  type: TOGGLE_IS_FOLLOWING_PROGRESS,
  isFetching,
  userId,
});
export const shiftPagesLeft = (beginPage, endPage) => ({
  type: SHIFT_PAGES_LEFT,
  beginPage,
  endPage,
});
export const shiftPagesRight = (beginPage, endPage) => ({
  type: SHIFT_PAGES_RIGHT,
  beginPage,
  endPage,
});
export const toggleIsFriends = (isFriends) => ({
  type: TOGGLE_IS_FRIENDS,
  isFriends,
});

export const requestUsers = (page, pageSize, isFriends) => {
  return (dispatch) => {
    dispatch(toggleIsFetching(true));
    usersAPI.getUsers(page, pageSize, isFriends).then((data) => {
      dispatch(toggleIsFetching(false));
      dispatch(setUsers(data.items));
      dispatch(setTotalUsersCount(data.totalCount));
    });
  };
};
export const requestFriends = (page, pageSize) => {
  return (dispatch) => {
    usersAPI.getUsers(page, pageSize, true).then((data) => {
      dispatch(setFriends(data.items));
    });
  };
};

export const follow = (userId) => {
  return (dispatch) => {
    dispatch(toggleFollowingProgress(true, userId));
    usersAPI.follow(userId).then((data) => {
      if (data.resultCode === 0) {
        dispatch(followSuccess(userId));
      }
      dispatch(toggleFollowingProgress(false, userId));
    });
  };
};
export const unfollow = (userId) => {
  return (dispatch) => {
    dispatch(toggleFollowingProgress(true, userId));
    usersAPI.unfollow(userId).then((data) => {
      if (data.resultCode === 0) {
        dispatch(unfollowSuccess(userId));
      }
      dispatch(toggleFollowingProgress(false, userId));
    });
  };
};

export default usersReducer;
